import React from 'react';
import { connect } from 'react-redux';
import {
  Card,
  CardBody,
  Col,
  Row,
} from 'reactstrap';

import { dashboardActions } from "../../_store/_actions";



class Dashboard extends React.Component {

  componentDidMount() {
    let estabelecimento = this.props.estabelecimento
    this.props.onLoadProdutos({ pagina: 1, limit: 1, estabelecimento })
  }

  logout = () => {
    //   this.props.dispatch(userActions.logout());
    this.props.history.push("/login");
  }


  render() {
    const { dashboard } = this.props
    return (
      <div className="animated fadeIn">
        <Row>
          {dashboard.resultado &&
            <Col xs="12" sm="6" lg="3">
              <Card className="text-white bg-info">
                <CardBody className="pb-0">

                  <div className="text-value">{Number(dashboard.resultado.todosProdutos.total).toLocaleString('pt-BR')}</div>
                  <div>Todos os Produtos</div>
                </CardBody>

              </Card>
            </Col>
          }
          {dashboard.resultado &&
            <Col xs="12" sm="6" lg="3">

              <Card className="text-white bg-success">
                <CardBody className="pb-0">

                  <div className="text-value">{Number(dashboard.resultado.meusProdutos.total).toLocaleString('pt-BR')}</div>
                  <div>Meus Produtos</div>
                </CardBody>


              </Card>
            </Col>
          }
        </Row>
      </div>

    );
  }
}

//export default Dashboard;

function mapStateToProps(state) {
  const { users, authentication, dashboard } = state;
  const { user } = authentication;
  const { estabelecimento } = user;

  return {
    user,
    users,
    dashboard,
    estabelecimento
  };
}

const mapDispatchToProps = dispatch => {
  return {
    onLoadProdutos: (filtros) => dispatch(dashboardActions.getProdutos(filtros)),
  }
}
//let connectedHomePage = connect(mapStateToProps)(Dashboard);
//export {connectedHomePage as Dashboard};
export default connect(mapStateToProps, mapDispatchToProps)(Dashboard);
